import axios from "axios";
const baseUrl = "http://kosa1.iptime.org:50319/";
function getorderPepole(ono) {
  return axios.get(baseUrl+"order/orderman?ono=" + ono);
}

function getBank(ono) {
  return axios.get(baseUrl+"order/selectBank?ono=" + ono);
}

function getCard(ono) {
  return axios.get(baseUrl+"order/selectCard?ono=" + ono);
}

function orderProduct(opno) {
  return axios.get(baseUrl+"order/getProduct?opno=" + opno);
}

function createOrder(orderSheet, orderProductList, payment, cartno) {
  console.log(orderSheet);
  console.log(orderProductList);
  let data = { orderSheet, orderProductList, payment, cartno };
  return axios.post(baseUrl+"order/createOrder", data);
}

function getBestProductList(count) {
  return axios.get(baseUrl+"order/bestproductlist?count=" + count);
}

function getOrderSheet(mno, ono) {
  return axios.get(`${baseUrl}order/get?ono=${ono}&mno=${mno}`);
}

function getOrderSheetList(mno, day) {
  return axios.get(`${baseUrl}order/getlist?day=${day}&mno=${mno}`);
}

function getPayment(ono) {
  return axios.get(`${baseUrl}order/getpayment?ono=${ono}`);
}

function getTotalOrderPrice(mno) {
  return axios.get(`${baseUrl}order/totalorderprice?mno=${mno}`);
}

export default {
  getorderPepole,
  getBank,
  getCard,
  orderProduct,
  createOrder,
  getBestProductList,
  getOrderSheet,
  getOrderSheetList,
  getPayment,
  getTotalOrderPrice,
};
