<!--  UI  -->
<template>
  <v-container class="mt-4 pt-0">
    <v-row justify="space-between">
      <v-col><h1 class="font-weight-bold text-h5 mb-1">Best Items</h1></v-col>
      <v-col v-if="isShowLink"
        ><router-link to="/home/bannerbest" style="text-decoration: none; color: inherit">
          <h5 class="text-end"><v-icon>mdi-chevron-right</v-icon></h5>
        </router-link></v-col
      >
    </v-row>
    <v-card flat>
      <v-container fluid>
        <v-item-group v-model="imgIndex">
          <v-row dense gutters>
            <v-col v-for="(product, index) in ProductList" :key="product.pcode" :cols="6">
              <v-item>
                <v-card elevation="0" width="100%">
                  <v-img :lazy-src="product.color[0].imgurl1" :src="product.color[0].imgurl1" class="white--text" width="100%" min-height="250px">
                    <v-row>
                      <v-col
                        ><v-sheet color="#333333" class="d-flex align-center justify-center rounded-br-lg" height="30" width="30">
                          <div class="orange--text"><v-icon></v-icon>{{ index + 1 }}</div>
                        </v-sheet></v-col
                      >
                      <v-col class="d-flex justify-end">
                        <v-btn icon dark @click="iWish(product.pcode)">
                          <v-icon>
                            {{ checkWish(product.pcode) ? "mdi-heart" : "mdi-heart-outline" }}
                          </v-icon>
                        </v-btn>
                      </v-col>
                    </v-row>
                    <v-container @click="print(product.pcode)" fill-height class="pa-0"> </v-container>
                  </v-img>
                  <div class="font-weight-light font-14px">{{ product.pbrand }}</div>
                  <div class="font-weight-bold font-14px">{{ product.pname }}</div>
                  <div class="font-14px" :class="product.pdiscount == 0 ? '' : 'grey--text'">
                    {{ wonChange(product.pprice) }}<span class="ml-2 main-txt" v-if="product.pdiscount != 0">{{ product.pdiscount }}%</span>
                  </div>
                  <div class="font-14px" v-if="product.pdiscount != 0">
                    {{ wonChange(product.pprice - Math.ceil(product.pprice * (product.pdiscount / 100))) }}
                  </div>
                </v-card>
              </v-item>
            </v-col>
          </v-row>
        </v-item-group>
      </v-container>
      <!--    -->
    </v-card>
  </v-container>
</template>

<script>
import apiOrder from "@/apis/order";
import apiProduct from "@/apis/product";
import WishApi from "@/apis/wish";
export default {
  //  (devtools  )
  name: "BannerBest",
  //    (import something from "/path")
  components: {},
  //   
  data() {
    return {
      ProductList: [],
      imgIndex: 0,
      isShowLink: true,
    };
  },
  //   
  methods: {
    getBestProductList(count) {
      apiOrder.getBestProductList(count).then((response) => {
        let data = response.data;
        this.getBestProductDetailList(data);
      });
    },
    getBestProductDetailList(bestProductList) {
      console.log(bestProductList);
      apiProduct.getBestProductList(bestProductList).then((response) => {
        let data = response.data;
        this.ProductList = data;
      });
    },
    async iWish(val) {
      // db store wish   
      if (this.$store.getters.getMno != null && this.$store.getters.getMno != 0) {
        const li = this.$store.getters.getWishlist;
        if (li == null || !li.includes(val)) {
          // add
          console.log("add");
          this.$store.commit("addWishlist", val);
          try {
            const promise = await WishApi.addWish({ mno: this.$store.getters.getMno, pcode: val });
            apiProduct.updateLike({ pcode: val, amount: 1 });
            console.log(promise);
          } catch (error) {
            console.log(error);
          }
        } else {
          console.log("delete");
          this.$store.commit("removeWishlist", val);
          try {
            const promise = await WishApi.deleteWish({ mno: this.$store.getters.getMno, pcode: val });
            apiProduct.updateLike({ pcode: val, amount: 1 });
            console.log(promise);
          } catch (error) {
            console.log(error);
          }
        }
      } else {
        console.log(" ");
      }
    },
    checkWish(val) {
      // wish  
      const li = this.$store.getters.getWishlist;
      if (li == null) {
        return false;
      } else {
        return li.includes(val);
      }
    },
    wonChange(num) {
      return String(num).replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
    },
    print(val) {
      this.$router.push(`/product/productdetail?pcode=${val}`);
    },
    showLink() {
      console.log(this.$route.path);
      let path = this.$route.path;
      if (path.startsWith("/home/bannerbest")) {
        this.isShowLink = false;
      } else {
        this.isShowLink = true;
      }
    },
  },
  created() {
    this.showLink();
    if (this.isShowLink) {
      this.getBestProductList(4);
    } else {
      this.getBestProductList(30);
    }
  },
  watch: {
    $route() {
      this.showLink();
    },
  },
};
</script>

<!--    -->
<style scoped>
.active-tab {
  color: black;
  font-weight: bold;
}
</style>
