import axios from "axios";
const baseUrl = "http://kosa1.iptime.org:50219/";
function login(member) {
  return axios.post(baseUrl + "member/login", {
    id: member.id,
    password: member.password,
  });
}

function join(member) {
  return axios.post(baseUrl + "member/join", {
    id: member.id,
    name: member.name,
    password: member.password,
    email: member.email,
    birth: member.birth,
    phone: member.phone,
  });
}

function coupon(mno) {
  return axios.get(baseUrl + "member/coupon?mno=" + mno);
}

function allCoupon(mno) {
  return axios.get(baseUrl + "member/allcoupon?mno=" + mno);
}

function getMember(mno) {
  return axios.get(baseUrl + "member/getmember?mno=" + mno);
}

function modifyMember(member) {
  return axios.patch(baseUrl + "member/modify", {
    mno: member.mno,
    email: member.email,
    phone: member.phone,
  });
}

function mileageList(mno, month) {
  return axios.get(baseUrl + "member/mileage?mno=" + mno + "&month=" + month);
}

function mileagepoint(mno) {
  return axios.get(baseUrl + "member/mileagepoint?mno=" + mno);
}

function getKakaoToken(code) {
  return axios.get(baseUrl + "member/kakaologin?authorize_code=" + code);
}

function joinKaKao(member, token) {
  return axios.post(baseUrl + "member/kakaojoin?access_token=" + token, {
    email: member.email,
    birth: member.birth,
    phone: member.phone,
    name: member.name,
    id: member.id,
    password: member.password,
  });
}

function logoutKakao() {
  return axios.get(baseUrl + "member/kakaologout");
}

function addAddress(addr, id) {
  if (id != "" && id) {
    return axios.post(baseUrl + "member/newaddress?id=" + id, {
      aname: addr.aname,
      addr1: addr.addr1,
      addr2: addr.addr2,
      addr3: addr.addr3,
      toname: addr.toname,
      tophone: addr.tophone,
      isdefault: addr.isdefault,
    });
  } else {
    console.log("run");
    return axios.post(baseUrl + "member/newaddress?id=", {
      aname: addr.aname,
      addr1: addr.addr1,
      addr2: addr.addr2,
      addr3: addr.addr3,
      toname: addr.toname,
      tophone: addr.tophone,
      isdefault: addr.isdefault,
      mno: addr.mno,
    });
  }
}

function getAddress(mno) {
  return axios.get(baseUrl + "member/getaddress?mno=" + mno);
}

function deleteAddress(mno, ano) {
  return axios.delete(`${baseUrl}member/deleteaddress?mno=${mno}&ano=${ano}`);
}

function modifyAddress(addr) {
  console.log(addr);
  return axios.put(baseUrl + "member/modifyaddress", addr);
}

function deletMember(member) {
  return axios.patch(baseUrl + "member/delete", member);
}

export default {
  login,
  join,
  coupon,
  getMember,
  modifyMember,
  allCoupon,
  mileageList,
  mileagepoint,
  getKakaoToken,
  joinKaKao,
  logoutKakao,
  addAddress,
  getAddress,
  deleteAddress,
  modifyAddress,
  deletMember,
};
