<template>
  <div class="d-flex">
    <v-btn class="pa-0" to="/product/recentproductlist">
      <span class="font-weight-bold">recent</span>
      <v-icon>mdi-history</v-icon>
    </v-btn>

    <v-btn class="pa-0" :to="$store.getters.getMemberId ? '/product/wishproductlist' : '/member/login'">
      <span class="text-sm font-weight-bold">like</span>
      <v-icon>mdi-cards-heart-outline </v-icon>
    </v-btn>

    <v-btn class="pa-0" to="/home/main">
      <span class="text-sm font-weight-bold">home</span>
      <v-icon>mdi-home-outline </v-icon>
    </v-btn>

    <v-btn class="pa-0" @click="myPage($store.getters.getMemberId !== null)">
      <span class="text-sm font-weight-bold">my</span>
      <v-icon color="gray">mdi-account-outline </v-icon>
    </v-btn>

    <v-btn class="pa-0">
      <span @click="handleDrawer" class="text-sm font-weight-bold">menu</span>
      <v-icon>mdi-menu</v-icon>
    </v-btn>
  </div>
</template>

<script>
export default {
  name: "MainFooter",
  data: () => ({}),
  methods: {
    myPage(flag) {
      // flag   
      if (flag) {
        this.$router.push("/member/mypage").catch(() => {});
      } else {
        this.$router.push("/member/login").catch(() => {});
      }
    },
    handleDrawer() {
      console.log("footer handleDrawer ");
      this.$emit("footer-handleDrawer");
    },
  },
};
</script>

