import axios from "axios";
const baseUrl = "http://kosa1.iptime.org:50419/";
function addWish(wish) {
  return axios.post(baseUrl+"wish/add", wish);
}

function getWishList(mno) {
  return axios.get(baseUrl+"wish/list?mno=" + mno);
}

function getStringWishList(mno) {
  return axios.get(baseUrl+"wish/stringlist?mno=" + mno);
}

function deleteWish(wish) {
  return axios.patch(baseUrl+"wish/delete", wish);
}

export default {
  addWish,
  getWishList,
  getStringWishList,
  deleteWish,
};
