import axios from "axios";
const baseUrl = "http://kosa1.iptime.org:50419/";
function getMenuCategory() {
  return axios.get(baseUrl+"product/menucategory");
}
function getSubCategory(ccode) {
  return axios.get(baseUrl + "product/subcategory?ccode=" + ccode);
}
function getBrandCategory() {
  return axios.get(baseUrl + "product/brandcategory");
}
function getLCategory() {
  return axios.get(baseUrl + "product/lcategory");
}

export default {
  getMenuCategory,
  getSubCategory,
  getBrandCategory,
  getLCategory,
};
