<!--  UI  -->
<template>
  <v-container class="mt-4 pt-0">
    <v-row justify="space-between">
      <v-col><h1 class="font-weight-bold text-h5 mb-1">New Items</h1></v-col>
      <v-col
        ><router-link to="/home/newproductlist" style="text-decoration: none; color: inherit"
          ><h5 class="text-end"><v-icon>mdi-chevron-right</v-icon></h5></router-link
        ></v-col
      >
    </v-row>

    <!--   brand, category -->
    <v-tabs active-class="active-tab" slider-color="black" v-model="tab">
      <v-tab v-for="tab in tablist" :key="tab.key">{{ tab.name }}</v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item v-for="items in itemlist" :key="items.key">
        <v-card flat>
          <v-sheet class="mx-auto mt-2" max-width="700">
            <!--    -->
            <v-slide-group mandatory>
              <v-slide-item v-for="category in items.categoryList" :key="category" v-slot="{ active, toggle }">
                <v-btn
                  class="mx-1"
                  :input-value="active"
                  active-class="grey white--text"
                  depressed
                  rounded
                  @click="toggle"
                  v-on:click="
                    {
                      {
                        items.key ? getBrandProductList(category) : getLcategoryProductList(category);
                      }
                    }
                  "
                >
                  {{ category }}
                </v-btn>
              </v-slide-item>
            </v-slide-group>
            <!--    -->
            <!--    -->
            <v-sheet class="mx-auto" min-height="300px" max-width="800" out>
              <v-slide-group :v-model="imgIndex" class="pa-2">
                <v-slide-item v-for="product in items.productList.productList" :key="product.pcode">
                  <v-card elevation="0" class="mr-3" width="150px">
                    <v-img :lazy-src="product.color[0].imgurl1" :src="product.color[0].imgurl1" class="white--text" width="100%" min-height="250px">
                      <v-row>
                        <v-col class="d-flex justify-end">
                          <v-btn icon dark @click="iWish(product.pcode)">
                            <v-icon>
                              {{ checkWish(product.pcode) ? "mdi-heart" : "mdi-heart-outline" }}
                            </v-icon>
                          </v-btn>
                        </v-col>
                      </v-row>
                      <v-container @click="print(product.pcode)" fill-height class="pa-0"> </v-container>
                    </v-img>
                    <div class="font-weight-light font-14px">{{ product.pbrand }}</div>
                    <div class="font-weight-bold font-14px">{{ product.pname }}</div>
                    <div class="font-14px" :class="product.pdiscount == 0 ? '' : 'grey--text'">
                      {{ wonChange(product.pprice) }}<span class="ml-2 main-txt" v-if="product.pdiscount != 0">{{ product.pdiscount }}%</span>
                    </div>
                    <div class="font-14px" v-if="product.pdiscount != 0">
                      {{ wonChange(product.pprice - Math.ceil(product.pprice * (product.pdiscount / 100))) }}
                    </div>
                  </v-card>
                </v-slide-item>
              </v-slide-group>
            </v-sheet>
            <!--    -->
          </v-sheet>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
  </v-container>
</template>

<script>
import apiCategory from "@/apis/category";
import apiProduct from "@/apis/product";
import WishApi from "@/apis/wish";
export default {
  //  (devtools  )
  name: "BannerNew",
  //    (import something from "/path")
  components: {},
  //   
  data() {
    return {
      tab: null,
      tablist: [
        { key: true, name: "BRAND" },
        { key: false, name: "CATEGORY" },
      ],
      itemlist: [
        { key: true, categoryList: [], productList: [] },
        { key: false, categoryList: [], productList: [] },
      ],
      imgIndex: 0,
    };
  },
  //   
  methods: {
    getBrandCategory() {
      apiCategory.getBrandCategory().then((response) => {
        let data = response.data;
        this.itemlist[0].categoryList = data;
        this.getBrandProductList(this.itemlist[0].categoryList[0]);
      });
    },
    getLcategory() {
      apiCategory.getLCategory().then((response) => {
        let data = response.data;
        this.itemlist[1].categoryList = data;
        this.getLcategoryProductList(this.itemlist[1].categoryList[0]);
      });
    },
    getBrandProductList(pbrand) {
      apiProduct.getBrandProductList(pbrand, 1).then((response) => {
        let data = response.data;
        this.itemlist[0].productList = data;
        this.imgIndex = 0;
      });
    },
    getLcategoryProductList(ccode) {
      apiProduct.getProductList(ccode, 1).then((response) => {
        let data = response.data;
        this.itemlist[1].productList = data;
        this.imgIndex = 0;
      });
    },

    async iWish(val) {
      // db store wish   
      if (this.$store.getters.getMno != null && this.$store.getters.getMno != 0) {
        const li = this.$store.getters.getWishlist;
        if (li == null || !li.includes(val)) {
          // add
          console.log("add");
          this.$store.commit("addWishlist", val);
          try {
            const promise = await WishApi.addWish({ mno: this.$store.getters.getMno, pcode: val });
            apiProduct.updateLike({ pcode: val, amount: 1 });
            console.log(promise);
          } catch (error) {
            console.log(error);
          }
        } else {
          console.log("delete");
          this.$store.commit("removeWishlist", val);
          try {
            const promise = await WishApi.deleteWish({ mno: this.$store.getters.getMno, pcode: val });
            apiProduct.updateLike({ pcode: val, amount: 1 });
            console.log(promise);
          } catch (error) {
            console.log(error);
          }
        }
      } else {
        console.log(" ");
      }
    },
    checkWish(val) {
      // wish  
      const li = this.$store.getters.getWishlist;
      if (li == null) {
        return false;
      } else {
        return li.includes(val);
      }
    },
    wonChange(num) {
      return String(num).replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
    },
    print(val) {
      this.$router.push(`/product/productdetail?pcode=${val}`);
    },
  },
  created() {
    this.getBrandCategory();
    this.getLcategory();
  },
};
</script>

<!--    -->
<style scoped>
.active-tab {
  color: black;
  font-weight: bold;
}
</style>
