<template>
  <v-navigation-drawer app v-model="localDrawer">
    <v-list-item>
      <v-list-item-content>
        <v-list-item-title class="text-h6">  </v-list-item-title>
      </v-list-item-content>
    </v-list-item>

    <v-divider></v-divider>

    <v-list dense nav>
      <v-list-item to="/">
        <v-list-item-content>
          <v-list-item-title>Home</v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-list-item to="/about">
        <v-list-item-content>
          <v-list-item-title>About</v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <!--  -->
      <v-list-group :value="false" active-class="black--text">
        <template v-slot:activator>
          <v-list-item-title></v-list-item-title>
        </template>
        <!-- L -->
        <v-list-group v-for="lcategory in menuCategory" :key="lcategory.lcpriority" :value="false" active-class="black--text" sub-group>
          <template v-slot:activator>
            <v-list-item-title>{{ lcategory.lcname }}</v-list-item-title>
          </template>
          <!-- M -->
          <v-list-item
            v-for="mcategory in lcategory.mcategory"
            color="black"
            exact
            exact-active-class="grey lighten-5"
            :key="mcategory.priority"
            @click="setSessionMainCategory(lcategory.lcname + '_' + mcategory.cname)"
            :to="`/product/productlist?ccode=${lcategory.lcname + '_' + mcategory.cname}`"
          >
            <v-list-item-content>
              <v-list-item-title>{{ mcategory.cname }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <!-- M -->
        </v-list-group>
        <!-- L -->
      </v-list-group>
      <!--  -->

      <v-list-item to="/home/bannerbest">
        <v-list-item-content>
          <v-list-item-title></v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-list-item to="/home/newproductlist">
        <v-list-item-content>
          <v-list-item-title></v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-list-item to="/event/list">
        <v-list-item-content>
          <v-list-item-title></v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>
    <v-row v-if="this.$store.getters.getMemberId !== null" class="mt-3">
      <v-col align="center">
        <v-btn color="#c2935f" class="font-weight-bold" block @click="logout"></v-btn>
      </v-col>
    </v-row>
  </v-navigation-drawer>
</template>
<script>
import category from "@/apis/category";
export default {
  //  (devtools  )
  name: "AppMenu",
  //    (import something from "/path")
  components: {},
  //   
  data() {
    return {
      localDrawer: this.drawer,
      menuCategory: null,
    };
  },
  //   
  methods: {
    logout() {
      if (this.$store.getters.getKToken != null) {
        this.$store.dispatch("deleteAuth");
        window.location.replace(
          "https://kauth.kakao.com/oauth/logout?client_id=443645dd1c1c1810cc2d2dd397f9d946&logout_redirect_uri=http://kosa1.iptime.org:50519/home/main",
        );
      } else {
        this.$store.dispatch("deleteAuth");
        this.$router.push("/home/main").catch(() => {});
      }
    },
    getMenuCategory() {
      category
        .getMenuCategory()
        .then((response) => {
          this.menuCategory = response.data;
        })
        .catch((error) => {
          if (error.response) {
            console.log(error.response.data);
          } else {
            console.log(" ");
          }
        });
    },
    setSessionMainCategory(mainCategory) {
      sessionStorage.setItem("mainCategory", mainCategory);
    },
  },
  watch: {
    drawer() {
      this.localDrawer = this.drawer;
    },
    localDrawer() {
      this.$emit("menu-handleDrawer", this.localDrawer);
    },
  },
  created() {
    this.getMenuCategory();
  },

  props: ["drawer"],
};
</script>

<!--    -->
<style scoped></style>
