import Vue from "vue";
import Vuex from "vuex";
import axiosConfig from "@/apis/axioisConfig";
import MemberApi from "@/apis/member";
import WishApi from "@/apis/wish";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    member: {
      memberId: null,
      mno: null,
      authToken: null,
      wishList: null,
      gname: null,
      coupon: null,
      mileagepoint: null,
    },
    kToken: null,
  },
  getters: {
    getMemberId(state) {
      return state.member.memberId;
    },
    getGname(state) {
      return state.member.gname;
    },
    getMno(state) {
      return state.member.mno;
    },
    getCoupon(state) {
      return state.member.coupon;
    },
    getMileagepoint(state) {
      return state.member.mileagepoint;
    },
    getKToken(state) {
      return state.kToken;
    },
    getWishlist(state) {
      return state.member.wishList;
    },
  },
  mutations: {
    setMember(state, payload) {
      state.member.memberId = payload.id;
      state.member.authToken = payload.jwt;
      state.member.gname = payload.gname;
    },
    setMno(state, payload) {
      state.member.mno = payload;
    },
    setCoupon(state, payload) {
      state.member.coupon = payload;
    },
    setMileagepoint(state, payload) {
      state.member.mileagepoint = payload;
    },
    setMemberObj(state, payload) {
      state.member = payload;
    },
    setKToken(state, payload) {
      state.kToken = payload;
    },
    addWishlist(state, payload) {
      if (state.member.wishList == null) {
        let list = [];
        list.push(payload);
        state.member.wishList = list;
      } else {
        state.member.wishList.push(payload);
      }
      sessionStorage.setItem("member", JSON.stringify(state.member));
    },
    removeWishlist(state, payload) {
      state.member.wishList.splice(state.member.wishList.indexOf(payload), 1);
      sessionStorage.setItem("member", JSON.stringify(state.member));
    },
    setWishlist(state, payload) {
      state.member.wishList = payload;
    },
    clear(state) {
      for (let key in state.member) {
        state.member[key] = null;
      }
      state.kToken = null;
    },
  },
  actions: {
    async saveAuth(context, payload) {
      context.commit("setMember", payload);
      context.commit("setMno", payload.mno);
      context.commit("setMileagepoint", payload.mileagepoint);
      context.commit("setKToken", payload.kToken);

      axiosConfig.addAuthHeader(payload.jwt);

      try {
        const coupons = await MemberApi.coupon(payload.mno);
        context.commit("setCoupon", coupons.data);
      } catch (error) {
        console.log(error);
      }

      try {
        const getwishlist = await WishApi.getStringWishList(payload.mno);
        context.commit("setWishlist", getwishlist.data.success);
      } catch (error) {
        console.log(error);
      }

      sessionStorage.setItem("member", JSON.stringify(this.state.member));
      sessionStorage.setItem("kToken", this.state.kToken);
    },

    loadAuth(context) {
      if (sessionStorage.getItem("member") != null) {
        context.commit("setMemberObj", JSON.parse(sessionStorage.getItem("member")));
      }
      if (sessionStorage.getItem("kToken") != null) {
        context.commit("setKToken", sessionStorage.getItem("kToken"));
      }

      if (context.state.member.authToken != "") {
        axiosConfig.addAuthHeader(context.state.member.authToken);
      }
    },

    async deleteAuth(context) {
      sessionStorage.removeItem("member");
      if (this.state.kToken != null && this.state.kToken.length > 0) {
        try {
          const promise = await MemberApi.logoutKakao();
          console.log(promise);
          sessionStorage.removeItem("kToken");
        } catch (error) {
          console.log(error);
        }
      }
      context.commit("clear");
      axiosConfig.removeAuthHeader();
    },
  },
  modules: {},
});
