<template>
  <v-app>
    <app-menu :drawer="drawer" @menu-handleDrawer="updateDrawer" />
    <main-header />

    <v-main>
      <router-view />
    </v-main>

    <v-bottom-navigation app v-if="flag">
      <main-footer @footer-handleDrawer="handleDrawer" />
    </v-bottom-navigation>

    <div class="foot" style="height: 40px"></div>
  </v-app>
</template>

<script>
import MainFooter from "./components/footer/MainFooter.vue";
import MainHeader from "./components/headers/MainHeader.vue";
import AppMenu from "@/components/menu/AppMenu.vue";
export default {
  name: "App",

  data: () => ({
    flag: true,
    drawer: false,
    scrollPosition: 0,
    beforePosition: 0,
  }),
  components: { MainHeader, MainFooter, AppMenu },
  methods: {
    handleScroll(e) {
      this.scrollPosition = e.target.documentElement.scrollTop;
      let path = this.$route.path;
      if (path.startsWith("/product/productdetail") || (path.startsWith("/order") && !path.startsWith("/order/orderList"))) {
        this.flag = false;
      } else {
        if (this.scrollPosition == 0 || this.beforePosition >= this.scrollPosition) {
          this.flag = true;
        } else {
          this.flag = false;
        }
      }
      this.beforePosition = this.scrollPosition;
    },
    updateDrawer(param) {
      console.log("updateDrawer ");
      this.drawer = param;
    },
    handleDrawer() {
      console.log("handleDrawer ");
      this.drawer = !this.drawer;
    },
  },
  created() {
    this.$store.dispatch("loadAuth");
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  watch: {
    $route(to) {
      if (to.path.startsWith("/product/productdetail")) {
        this.flag = false;
      }
      if (to.path.startsWith("/order")) {
        this.flag = false;
      }
    },
  },
};
</script>

<style>
.main-head {
  color: black;
  font-weight: bold;
  text-shadow: 1px 1px 1px gray;
}
.main-txt {
  color: #c2935f;
}
.font-14px {
  font-size: 14px;
}
</style>
