<template>
  <div class="pa-0 ma-0">
    <v-app-bar app :color="mainHeader ? 'black' : 'white'" dark :class="mainHeader ? '' : 'subHeader'" hide-on-scroll>
      <template v-if="mainHeader">
        <span style="font-size: 28px" @click="$router.push('/home/main').catch(() => {})"></span>
        <v-spacer></v-spacer>

        <v-btn icon>
          <v-icon>mdi-magnify</v-icon>
        </v-btn>

        <v-btn icon to="/order/cart">
          <v-icon>mdi-shopping-outline</v-icon>
        </v-btn>
      </template>
      <template v-else>
        <v-btn icon @click="back">
          <v-icon color="black">mdi-chevron-left</v-icon>
        </v-btn>

        <v-spacer></v-spacer>

        <v-btn icon color="black">
          <v-icon>mdi-magnify</v-icon>
        </v-btn>
        <v-btn icon to="/order/cart">
          <v-icon color="black">mdi-shopping-outline</v-icon>
        </v-btn>
      </template>

      <template v-slot:extension v-if="mainHeader">
        <v-tabs class="sticky" show-arrows>
          <v-tab :key="1" to="/home/main">Home</v-tab>
          <v-tab :key="2" to="/home/bannerbest">Best</v-tab>
          <v-tab :key="3" to="/home/newproductlist">New</v-tab>
          <v-tab :key="4" to="/event/list">Event</v-tab>
        </v-tabs>
      </template>
    </v-app-bar>
  </div>
</template>

<script>
const reg = /^(\/member|\/product|\/order)/;
export default {
  name: "MainHeader",
  data: () => ({
    mainHeader: true,
  }),
  methods: {
    move(val) {
      this.$router.push(val).catch(() => {});
    },
    back() {
      this.$router.back();
    },
    change(val) {
      if (reg.test(val)) {
        console.log("is subheader");
        this.mainHeader = false;
      } else {
        this.mainHeader = true;
      }
    },
  },
  watch: {
    $route: function () {
      this.change(this.$route.path);
    },
  },
};
</script>

<style scoped>
.subHeader {
  background-color: rgba(255, 255, 255, 0.5);
}
</style>
