<!--  UI  -->
<template>
  <div class="container ma-0 pa-0">
    <v-carousel
      cycle
      :interval="interval"
      hide-delimiters
      show-arrows-on-hover
      :touch="{
        left: () => activeCarousel(1),
        right: () => activeCarousel(-1),
      }"
      :value="activeSlide"
    >
      <v-carousel-item class="ma-0 pa-0" v-for="(event, i) in eventList" :key="i" :to="'/event/list/detail?eno=' + event.eno" :src="event.eimgurl">
        <v-sheet class="ma-0 pa-0" height="100%">
          <v-img class="rounded-bl-xl" :src="event.eimgurl">
            <v-row style="height: 100%" no-gutters align="end">
              <v-col class="ml-4 mb-4 mr-4"
                ><div class="subtitle-2 white--text mb-2">{{ event.ebrand }}</div>
                <div class="text-h4 white--text mb-3">{{ event.etitle }}</div>
                <div class="text-h6 white--text mb-2">{{ event.econtent }}</div>
                <div class="d-flex flex-row-reverse subtitle-2 white--text">{{ i + 1 }} / {{ eventList.length }}</div>
              </v-col>
            </v-row>
          </v-img>
        </v-sheet>
      </v-carousel-item>
    </v-carousel>
  </div>
</template>

<script>
import EventApi from "@/apis/event";
export default {
  //  (devtools  )
  name: "BannerEvent",
  //    (import something from "/path")
  components: {},
  //   
  data() {
    return {
      activeSlide: 0,
      eventList: [],
      interval: 3000,
    };
  },
  //   
  methods: {
    activeCarousel(val) {
      this.activeSlide = (this.activeSlide + val + this.eventList.length) % this.eventList.length;
    },
    getEventList() {
      EventApi.eventlist().then((response) => {
        let data = response.data.progress;
        console.log(data);
        this.eventList = data;
      });
    },
  },
  created() {
    this.getEventList();
  },
};
</script>

