import axios from "axios";
const baseUrl = "http://kosa1.iptime.org:50419/";
const baseUrl2 = "http://kosa1.iptime.org:50319/";

function getProductList(ccode, pageNo) {
  // return axios.get("product/productlist", { pageno: pageNo, ccode: ccode });
  return axios.get(baseUrl+"product/productlist?ccode=" + ccode + "&pageno=" + pageNo);
}

function getBrandProductList(pbrand, pageNo) {
  return axios.get(baseUrl+"product/brandproductlist?pbrand=" + pbrand + "&pageno=" + pageNo);
}

function getProductDetail(pcode) {
  return axios.get(baseUrl+"product/productdetail?pcode=" + pcode);
}

function getProductSizeAmount(pcode, pcolor) {
  return axios.get(baseUrl2 + "productstock/getsizeamount?pcode=" + pcode + "&pcolor=" + pcolor);
}

function getBestProductList(bestProductList) {
  return axios.post(baseUrl+"product/bestproductlist", JSON.stringify(bestProductList), {
    headers: { "Content-Type": "application/json" },
  });
}

function updateLike(obj) {
  return axios.post(baseUrl+"product/updateLike", JSON.stringify(obj), {
    headers: { "Content-Type": "application/json" },
  });
}

function getProductQnaList(pageno, mno, pcode) {
  return axios.get(baseUrl+"product/productqnalist?pageno=" + pageno + "&mno=" + mno + "&pcode=" + pcode);
}

function getProductQnaNum(pcode) {
  return axios.get(baseUrl+"product/productqnacount?pcode=" + pcode);
}

function addQna(qna) {
  return axios.post(baseUrl+"product/addproductqna", {
    pcode: qna.pcode,
    qtitle: qna.qtitle,
    qcontent: qna.qcontent,
    islock: qna.islock,
    mno: qna.mno,
    id: qna.id,
  });
}

export default {
  getProductList,
  getProductDetail,
  getProductSizeAmount,
  getBrandProductList,
  getBestProductList,
  updateLike,
  getProductQnaList,
  getProductQnaNum,
  addQna,
};
